class CountdownTimer{
  constructor({selector,targetDate}){
this.selector = selector;
this.targetDate = targetDate;}
getFullInfo(){
  const timer = document.querySelector(this.selector);
 
    let interval;

  interval = setInterval(() => {
  const time = this.targetDate.getTime() - Date.now();

  const days = Math.floor(time / (1000 * 60 * 60 * 24));

  const hours = Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));

  const mins = Math.floor((time % (1000 * 60 * 60)) / (1000 * 60));

  const secs = Math.floor((time % (1000 * 60)) / 1000);

timer.textContent = (`${days} Days : ${hours} Hours : ${mins} Minutes : ${secs} Seconds`)
  }, 1000);
 
  
};}
  
const initialTimer = new CountdownTimer({
  selector: '#timer-1',
  targetDate: new Date('Jul 17, 2020'),

});
initialTimer.getFullInfo()




